  flex-direction: column;
}

.nova-dashboard-single_question_title_view {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.8rem;
}

.nova-dashboard-single_question_title_view h3 {
  font-family: "Comfortaa";
  font-weight: 700;
  font-size: 2.4rem;
  line-height: 2.7rem;
  color: #292929;
  padding-left: 1.5rem;
}

.nova-dashboard-single_question_title_view img {
  width: 3.4rem;
  height: 3.4rem;
  object-fit: contain;
  cursor: pointer;

  transition-duration: 0.8s;
  transition-property: transform;
  /* overflow: hidden; */
}

.nova-dashboard-single_question_title_view img:hover {
  transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}
.nova-dashboard-single_question_divider {
  display: flex;
  border: 1px solid #3d3d3d;
  margin-bottom: 4rem;
}

.nova-dashboard-single_question_view-h4 {
  animation: growDon 3s ease-in forwards;
  transition-duration: 3s;
  transition-timing-function: linear;
  width: 70%;
}
.nova-dashboard-single_question_view-h4 h4 {
  font-family: "Poppins";
  font-weight: 400;
  font-size: 2rem;
  line-height: 3rem;
  color: #3d3d3d;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 1.5rem;
  animation: growDown 600ms ease-out both;
}
@keyframes growDown {
  0% {
    transform: scale(0);
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
